import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthenticatedGuard } from '../providers/authenticated-guard/authenticated-guard';
import { AuthenticatedRole } from '../providers/authenticated-role/authenticated-role';
import { LoopBackConfig }  from '../shared/loopback-sdk';

//Layouts
import { FullLayoutComponent } from './layouts/full-layout.component';
import { LoginComponent } from './pages/login/login.component';

export const routes: Routes = [{
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
	},
	{
		path: 'login',
		loadChildren: './pages/login/login.module#LoginModule',
		data: {
			title: 'Login'
		}
	}, 
	{
		path: 'password-forget',
		loadChildren: './pages/password-forget/password_forget.module#PasswordForgetModule',
		data: {
			title: 'Password Forget?'
		}
	}, 
	{
		path: 'password-reset/:token',
		loadChildren: './pages/password-reset/password_reset.module#PasswordResetModule',
		data: {
			title: 'Password Forget?'
		}
	}, 
	{
		path: '',
		component: FullLayoutComponent,
		data: {
			title: 'Home'
		},
		children: [
			{
				path: 'dashboard',
				loadChildren: './dashboard/dashboard.module#DashboardModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
            {
				path: 'books',
				loadChildren: './pages/books/book/books.module#BooksModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/view-book/:id',
				loadChildren: './pages/books/view-book/view-book.module#ViewBookModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/add-book',
				loadChildren: './pages/books/add-book/add-book.module#AddBooksModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/edit-book/:id',
				loadChildren: './pages/books/edit-book/edit-book.module#EditBookModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/view-chapter/:id',
				loadChildren: './pages/chapters/view-chapter/view-chapter.module#ViewChapterModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/add-chapter/:id',
				loadChildren: './pages/chapters/add-chapter/add-chapter.module#AddChapterModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/edit-chapter/:id',
				loadChildren: './pages/chapters/edit-chapter/edit-chapter.module#EditChapterModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/add-page/:id',
				loadChildren: './pages/pages/add-page/add-page.module#AddPageModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'contractor',
				loadChildren: './pages/contractor/contractor/contractor.module#ContractorModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'contractor/add-contractor',
				loadChildren: './pages/contractor/add-contractor/add-contractor.module#AddContractorModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'contractor/view-contractor/:id',
				loadChildren: './pages/contractor/view-contractor/view-contractor.module#ViewContractorModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'contractor/edit-contractor/:id',
				loadChildren: './pages/contractor/edit-contractor/edit-contractor.module#EditContractorModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/view-page/:id',
				loadChildren: './pages/pages/view-page/view-page.module#ViewPageModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'books/edit-page/:id',
				loadChildren: './pages/pages/edit-page/edit-page.module#EditPageModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'videos',
				loadChildren: './pages/videos/video/videos.module#VideosModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'videos/add-video',
				loadChildren: './pages/videos/add-video/add-video.module#AddVideoModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'videos/edit-video/:id',
				loadChildren: './pages/videos/edit-video/edit-video.module#EditVideoModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'projects/:id',
				loadChildren: './pages/projects/project/projects.module#ProjectsModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: 'projects/view-project/:id',
				loadChildren: './pages/projects/view-project/view-project.module#ViewProjectModule',
				canActivate: [AuthenticatedGuard,AuthenticatedRole]
			},
			{
				path: '**',
				redirectTo: 'login'
			},
			
		]
	}
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {}



