import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { CookieService} from 'angular2-cookie/core';
import { LoopBackConfig, LoopBackAuth, UserApi, App_usersApi }  from '../../shared/loopback-sdk';
declare var sdkPrefix;
@Injectable()

export class AuthenticatedRole implements CanActivate {
    
	userData:any = {};
	roleName:any;
	constructor(private router: Router,private _cookieService:CookieService, private loopAuth:LoopBackAuth, private dApi:App_usersApi){
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.dApi.getUserRole(loginFlagToken).subscribe(
			data=>{
				if(data){
					this.roleName = data.result[0].name;
				}else{
					this.roleName = "Contractor"
				}
			},
			(err)=>{},
			()=>{}
		)
    }

    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot){
		var currentUrl = state.url;
		if((this.roleName == "Contractor")){
			this.router.navigate(['/login', {}]);
			return false;
		}else{
			return true;
		}
    }
}


