import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoopBackConfig, LoopBackAuth, UserApi, App_usersApi }  from '../../shared/loopback-sdk';
import {Subscription} from 'rxjs';
import {Observable} from 'rxjs/Observable';
import * as moment from 'moment';
import {CookieService} from 'angular2-cookie/core';
import { ShareService } from '../../providers/share-service/share-service';
declare var EventSource:any;
declare var sdkPrefix;
@Component({
  selector: 'app-dashboard',
  templateUrl: './full-layout.component.html'
})
export class FullLayoutComponent implements OnInit {
	currUserData:any=null;
	menuKnowledgebank:any =[];
	menuProfolio:any=[];
	menuReward:any = [];
	menuContractor:any = [];
	menuReport:any = [];
	menuUpload:any = [];
	menuEap:any = [];
	loading:boolean=false;
	defaultRoles:any = "";
	menuItem:any = "";
	username:any;
	busy:any;
	currUsernameDisplay:any="Admin";
	accTokUrl:String="access_token=";
	constructor(private router:Router,private userApi:UserApi,private _cookieService:CookieService,public sharS:ShareService, private loopAuth:LoopBackAuth, private uApi:App_usersApi) { 
		//var userDetails = this.loopAuth.getCurrentUserData();
		//this.username = userDetails.realm;
		//this.defaultRoles = userDetails.userDet.roles[0].name;
		
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.uApi.getUserRole(loginFlagToken).subscribe(
			data=>{
				this.defaultRoles = data.result[0].name;
				this.username = data.result[0].realm;
				this.menuKnowledgebank = [
					{
						"link":"/books",
						"name":"View Books"
					},
					{
						"link":"/videos",
						"name":"Videos"
					}
				];

				this.menuProfolio = [
					{
						"link":"/projects/all",
						"name":"View Projects"
					}
				];
				this.menuContractor = [
					{
						"link":"/contractor",
						"name":"Contractors"
					}
				];
			},
			(err)=>{},
			()=>{}
		)
	}
	
	public disabled:boolean = false;
	public status:{isopen:boolean} = {isopen: false};
	public toggled(open:boolean):void {
		//console.log('Dropdown is now: ', open);
	}

	public toggleDropdown($event:MouseEvent):void {
		$event.preventDefault();
		$event.stopPropagation();
		this.status.isopen = !this.status.isopen;
	}

	logOut(){
		this.loading = true;
		this.userApi.logout().subscribe((dataLogout)=>{
			this.loading = false;
			//console.log('dataLogout',dataLogout);
			this.sharS.setshareData('currUserData',null);
			this.sharS.setshareData('accTokUrl',null);
			this.router.navigate(["/login"]);
		},(dataError)=>{
			//console.log('dataError',dataError);
			this.loading = false;
			this.sharS.setshareData('currUserData',null);
			this.sharS.setshareData('accTokUrl',null);
			this.router.navigate(["/login"]);
		});
	}
	ngOnInit(): void {}
	ngAfterViewInit(): void {
		//console.log('ngOnInit on full');
		let currentUrl = this.router.url;
		if(currentUrl.includes('books') || currentUrl.includes("add-book") || currentUrl.includes("view-book") || currentUrl.includes('videos') || currentUrl.includes("add-video") || currentUrl.includes("edit-video")){
			this.menuItem = 'knowledgebank';
		}else if(currentUrl.includes("projects") || currentUrl.includes("view-project")){
			this.menuItem = 'profolio';
		}else if(currentUrl.includes("contractor")){
			this.menuItem = 'contractor';
		}

		console.log("menuItem", this.menuItem);

		let currUserData  = this._cookieService.get(sdkPrefix+'user');
		let accessToken = this._cookieService.get(sdkPrefix+'id');
		this.accTokUrl+=accessToken;
		this.sharS.setshareData('accTokUrl',this.accTokUrl);
		if (currUserData) {
			this.currUserData = JSON.parse(currUserData);
			let userType = null;
			let userAreaData={};
			this.currUsernameDisplay = this.currUserData['realname'];
		}else{
			this.router.navigate(["/login"]);
		}
	}

	changeMenu(menu){
		this.menuItem = menu;
		if(menu == 'knowledgebank'){
			this.router.navigate(["/books"]);
		}else if(menu == "contractor"){
			this.router.navigate(["/contractor"]);
		}
		else if(menu == "profolio"){
			this.router.navigate(["/projects/all"]);
		}
	}
	
	getUserNameEmail(str){
		let nameParts = str.split("@");
		let name = nameParts.length==2 ? nameParts[0] : null;
		return name;
	}
}


