import { Injectable } from '@angular/core';
import { CookieService } from 'angular2-cookie/core';
import { Http } from '@angular/http'; 
import {BusyModule} from 'angular2-busy';
declare var hostname:any;
declare var sdkPrefix;
@Injectable()
export class ShareService {  
  
   public shareData:any;
   busy:any;
 
    constructor(private _cookieService:CookieService, private http:Http) {
      this.shareData={};
    }

    setshareData(key,value) {
        this.shareData[key]=value;
    }
    getshareData(key) {
        return this.shareData[key]?this.shareData[key]:false;
    }  

    parsePageToReplaceImg(content){
		return new Promise((resolve,reject)=>{
			let tempImageArray:any= [];
			const parser = new DOMParser();
			let content1 = parser.parseFromString(content,"text/html");
			if(content1.images.length > 0){
				var promises = [];
				for(let i=0;i<content1.images.length;i++){
					var n = content1.images[i].src.includes("data:image");
					var isBase64Valid = true;
					if(n){
						var block = content1.images[i].src.split(";");
						var contentType = block[0].split(":")[1];
						var extension = block[0].split("/")[1];
						var realData = block[1].split(",")[1];
						var base64Rejex = /^([0-9a-zA-Z+/]{4})*(([0-9a-zA-Z+/]{2}==)|([0-9a-zA-Z+/]{3}=))?$/;
						isBase64Valid = base64Rejex.test(realData); // base64Data is the base64 string
					}else{
						isBase64Valid = false;
					}

					if (isBase64Valid){
						tempImageArray[i]=content1.images[i].src;
						console.log('----------------------------------');
						let promise = this.uploadImageAfterBlob(content1.images[i].src,i,'yes');
						promises.push(promise);
					}else{
						tempImageArray[i]=content1.images[i].src;
						let promise = this.uploadImageAfterBlob(content1.images[i].src,i,'no');
						promises.push(promise);
					}
				}
				Promise.all(promises).then((data)=>{
					// console.log("data",data);
					// console.log("tempImageArray",tempImageArray);
					for(var i=0; i<data.length; i++){
						let dataKey = data[i].split("->>"); 
						// console.log("replace", tempImageArray[dataKey[0]]);
						console.log("replace By ", dataKey[1]);
						content = content.replace('"'+tempImageArray[dataKey[0]]+'"','"'+dataKey[1]+'"');
					}
					// console.log("content Outside", content);
					resolve(content);
				});
			}else{
				resolve(content);
			}
		});
	}

	async uploadImageAfterBlob(imageUrl,index, valid){
		// Split the base64 string in data and contentType
		if(valid == 'yes'){
			var block = imageUrl.split(";");
			var contentType = block[0].split(":")[1];
			var extension = block[0].split("/")[1];
			var realData = block[1].split(",")[1];
			var blob = await this.b64toBlob(realData, contentType, 512);
			var imageName = new Date().getTime()+"_"+"."+extension;
		}
		return new Promise((resolve,reject)=>{
			if(blob && valid == 'yes'){
				// Create a FormData and append the file with "image" as parameter name
				var formDataToUpload = new FormData();
				formDataToUpload.append("image", blob, imageName);
				let fileUrl = hostname+"/api/containers/logo_image";
				let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
				this.busy = this.http.post(fileUrl+'/upload?access_token='+loginFlagToken, formDataToUpload).subscribe(res=>{
					var dataimg = res['_body'];
					dataimg = JSON.parse(dataimg);
					if(dataimg){
                        let imgName = dataimg['result']['files']['image'][0]['name'];
                        let imgSrc = fileUrl+"/download/"+imgName;
						resolve(index+"->>"+imgSrc);
					}else{
						resolve(false);
					}
				});
			}else{
				let imgSrc = imageUrl.split("?access_token=");
				if(imgSrc){
					resolve(index+"->>"+imgSrc[0]);
				}else{
					resolve(index+"->>"+imageUrl);
				}
			}
		});
	}

	//change base64 to blob
	b64toBlob(b64Data, contentType, sliceSize) {
        contentType = contentType || '';
        sliceSize = sliceSize || 512;
        var byteCharacters = atob(b64Data);
        var byteArrays = [];
        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            var slice = byteCharacters.slice(offset, offset + sliceSize);
            var byteNumbers = new Array(slice.length);
            for (var i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }
            var byteArray = new Uint8Array(byteNumbers);
            byteArrays.push(byteArray);
        }
      	var blob = new Blob(byteArrays, {type: contentType});
     	return blob;
	}
}


