import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { HttpModule } from '@angular/http';

//Importing loopback sdk
import { SDKBrowserModule,LoopBackConfig } from '../shared/loopback-sdk';
import { LoginComponent } from './login/login.component';
import { AppRoutingModule } from './app.routing.module';
import { AuthenticationService } from './services/auth/authentication.service';

import { AuthenticatedGuard } from '../providers/authenticated-guard/authenticated-guard';
import { AuthenticatedRole } from '../providers/authenticated-role/authenticated-role';

import { BreadcrumbsComponent } from './shared/breadcrumb.component';
//import { LayoutModule } from './layout/layout.module';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { FullLayoutComponent } from './layouts/full-layout.component';
import { BusyModule } from "angular2-busy"

import { CookieService } from 'angular2-cookie/services/cookies.service';
import { ShareService } from '../providers/share-service/share-service';
import { SIDEBAR_TOGGLE_DIRECTIVES } from './shared/sidebar.directive';
import { AsideToggleDirective } from './shared/aside.directive';
import { KeysPipe3 } from './shared/keyspipe.directive';
import { KeysPipe2 } from './shared/keyspipe2.directive';
import { NAV_DROPDOWN_DIRECTIVES } from './shared/nav-dropdown.directive';
import {LocationStrategy, HashLocationStrategy} from '@angular/common';
 

@NgModule({
  declarations: [
    AppComponent,
    FullLayoutComponent,
    LoginComponent,
    BreadcrumbsComponent,
    PageNotFoundComponent,
    ...SIDEBAR_TOGGLE_DIRECTIVES,
    AsideToggleDirective,
    KeysPipe3,
    KeysPipe2,
    ...NAV_DROPDOWN_DIRECTIVES

  ],
  imports: [
  AppRoutingModule,
  BrowserModule,
  BrowserAnimationsModule,
  SDKBrowserModule.forRoot(),
  BusyModule,
  HttpModule
  ],
  providers: [{
    provide: LocationStrategy,
    useClass: HashLocationStrategy
  },
 { provide: CookieService, useFactory: cookieServiceFactory },
  AuthenticatedGuard,
  AuthenticatedRole,
  ShareService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function cookieServiceFactory() { return new CookieService(); }



